									<div class="content">
										1、只有通过排行榜进入活动职位页才能生成专属分享链接，其他分享行为将无法记录分享效果；<br>
										2、一个好友7天内只能邀请1次，重复邀请将不计数；<br>
										3、好友只能邀请浏览1个职位，浏览多个职位将不计数；<br>
										4、分享多个职位后，邀请人数将合计；<br>
										5、如果分享的职位海报，好友需要扫描小程序码去浏览活动职位哦；<br>
										6、新用户浏览活动职位页需要授权，不允许授权系统将无法记录浏览行为；<br>
