<script>
import Vue from 'vue'
import Component from 'vue-class-component'
import wechatMixin from '@/mixins/wechat'
import {getRewardListApi, joinRewardListApi, getPositionQrcodeApi, postPositinApi,getInviteUserListApi, getInviteListTestApi, getIndexTestApi} from '@/store/api/activity'
import localstorage from '@/utils/localstorage'
let timer = null
@Component({
	components: {
  },
  mixins: [wechatMixin]
})
export default class rewardList extends Vue {
	cdnPath = `${process.env.VUE_APP_CDNPATH}/rewardList/`
	prohibit = false
	endTime = new Date('2019/3/8 20:34:00').getTime()
	curTime = new Date().getTime()
	centerPop = false
	bottomPop = false
	openGz = false
	timeStamp = 0
	tabIndex = 0 // tab 切换索引值
	positionIndex = 0 // 职位索引值
	hour = 0
	minute = 0
	second = 0
	activityEnd = false
	stand = 0
	info = {}
	rankData = {
		pageNum: 0,
		list: [],
		noData: false,
		pullUpStatus: false
	}
	user = {
	}
	city = ''
	qrcode = ''
	mounted() {
		this.joinList()
	}
	loadMode(){
		if (this.tabIndex === 1) {
			this.getData()
		}
	}
	getData () {
		let that = this
		this.rankData.pageNum ++
		this.rankData.pullUpStatus = true
		getRewardListApi({list_id: this.$route.query.id, page: this.rankData.pageNum}).then(res => {
			this.rankData.list = this.rankData.list.concat(res.data.ranks)
			this.rankData.noData = !res.meta.more
			this.rankData.pullUpStatus = false
			this.info = res.data.listInfo
			this.user = res.data.user
			this.endTime = this.info.endTime
			this.curTime = res.data.timestamp
			this.stand = res.data.stand
			this.timeStamp = (this.endTime - this.curTime) * 1000
			if (!this.user.location) {
				var geolocation = new qq.maps.Geolocation("TMZBZ-S72K6-66ISB-ES3XG-CVJC6-HKFZG", "myapp")
				geolocation.getIpLocation((res) => {
					this.city = res.city
					postPositinApi({list_id: this.$route.query.id, location: this.city, encode: 0, noLoading: true})
					console.log(res, '定位成功')
				}, (err) => {
					console.log(err, '定位失败')
				})
			}
			let shareDesc = ''
			that.info.positionList.map((item) => {
				shareDesc = `${shareDesc}${item.positionInfo.positionName}、`
			})
			shareDesc = shareDesc.slice(0, shareDesc.length-1)
			shareDesc = `本期职位：${shareDesc}`
			this.wechatShare({
	      title: '全民猎人·赏金职位榜，点击查看你的排名',
	      desc: shareDesc,
	      link: location.href.split('#')[0],
	      imgUrl: `${that.cdnPath}share3.png?a=1`
	    })
			if (this.timeStamp <= 0) {
				this.activityEnd = true
			}
			clearInterval(timer)
			timer = setInterval(() => {
				this.hour = parseInt(this.timeStamp / 3600000)
				this.minute = parseInt(this.timeStamp / 60000) % 60
				this.second = parseInt(this.timeStamp / 1000) % 60
				if (this.hour < 10) this.hour = `0${this.hour}`
				if (this.minute < 10) this.minute = `0${this.minute}`
				if (this.second < 10) this.second = `0${this.second}`
				if (this.timeStamp <= 0) {
					this.activityEnd = true
					clearInterval(timer)
				}
				this.timeStamp -= 1000
			}, 1000)
		})
	}

	toInviteMsg(item) {
		if(item.listId){
			localstorage.set('rewardCompanyUser', item)
			this.$router.push({
				path: `inviteMsg/${item.listId}`,
				query: {
					type: `personage`
				}
			})
		}
	}

	joinList () {
		joinRewardListApi({list_id: this.$route.query.id}).then(res => {
			this.getData()
		}).catch(e => {
			this.getData()
			if (e.code === -200) {
				this.prohibit = true
			}
		})
	}
	bottomBtn () {
		this.tabIndex = 0
		this.centerPop = false
		this.bottomPop = false
		Vue.toast({title: '请挑选1个或者多个职位进行分享'})
	}
	scrollCenter () {
		this.tabIndex = 0
		Vue.toast({title: '请挑选1个或者多个职位进行分享'})
	}
	openPop (type, index) {
		if (type === 'center') {
			this.centerPop = true
			this.bottomPop = false
			if (index) {
				this.positionIndex = index
			}
		} else {
			this.bottomPop = true
			this.centerPop = false
		}
	}
	colsePop() {
		this.centerPop = false
		this.bottomPop = false
	}
}
</script>
