import { request } from './request.js'

// 获取排行榜数据
export const getRewardListApi = (params) => request({
	url: `/activity/index/${params.list_id}`,
	method: 'get',
	params
})

//加入榜单
export const joinRewardListApi = (params) => request({
	url: `/activity/join/${params.list_id}`,
	method: 'get',
	params
})

//定位
export const postPositinApi = (params) => request({
	url: `/activity/location/${params.list_id}`,
	method: 'post',
	params
})

//获取(对应内推员的)受邀请人列表,需用户授权
export const getInviteUserListApi = (params) => request({
	url: `/activity/${params.list_id}/lists/invite`,
	method: 'get',
	params
})


//[测试]获取(对应内推员的)受邀请人列表,需用户授权
export const getInviteListTestApi = (params) => request({
	url: `/activity/test/${params.list_id}/lists/invite`,
	method: 'get',
	params
})

